import React, { ReactNode } from "react";

interface ScrollAreaProps {
  children: ReactNode;
  className?: string;
  maxHeight?: string;
}

export function ScrollArea({ 
  children, 
  className = "",
  maxHeight = "100%" 
}: ScrollAreaProps) {
  return (
    <div 
      className={`
        overflow-y-auto
        scrollbar-thin
        scrollbar-thumb-gray-300
        dark:scrollbar-thumb-gray-700
        scrollbar-track-transparent
        ${className}
      `}
      style={{ maxHeight }}
    >
      {children}
    </div>
  );
}
