import React, { ReactNode } from "react";

interface AlertProps {
  children: ReactNode;
  className?: string;
  variant?: "default" | "destructive";
}

interface AlertDescriptionProps {
  children: ReactNode;
  className?: string;
}

export function Alert({ 
  children, 
  className = "", 
  variant = "default" 
}: AlertProps) {
  const variantStyles = {
    default: "bg-blue-50 dark:bg-blue-900/20 text-blue-700 dark:text-blue-400 border-blue-200 dark:border-blue-800",
    destructive: "bg-red-50 dark:bg-red-900/20 text-red-700 dark:text-red-400 border-red-200 dark:border-red-800"
  };

  return (
    <div
      role="alert"
      className={`
        p-4 
        rounded-lg 
        border 
        ${variantStyles[variant]}
        ${className}
      `}
    >
      {children}
    </div>
  );
}

export function AlertDescription({ 
  children, 
  className = "" 
}: AlertDescriptionProps) {
  return (
    <div className={`text-sm mt-1 ${className}`}>
      {children}
    </div>
  );
}
