"use client";

import { useState } from "react";
import { Card } from "./ui/card";
import { Input } from "./ui/input";
import { Button } from "./ui/button";
import { Alert, AlertDescription } from "./ui/alert";
import { ScrollArea } from "./ui/scroll-area";
import { Loader2 } from "lucide-react";

interface Message {
  role: "user" | "assistant" | "system";
  content: string;
  metadata?: {
    target_domain?: string;
    plan_data?: PentestPlan;
  };
}

interface PentestTool {
  tool: string;
  command: string;
  description: string;
  risk_level: "low" | "medium" | "high";
  category: "network" | "web" | "infrastructure" | "social";
}

interface PentestPlan {
  plan: PentestTool[];
  target: string;
  assessment_type: string;
  estimated_time: string;
  prerequisites: string[];
  warnings: string[];
}

export default function ChatInterface() {
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim()) return;

    setLoading(true);
    setError("");
    
    try {
      const userMessage: Message = { role: "user", content: input };
      setMessages((prev) => [...prev, userMessage]);
      setInput("");

      const response = await fetch("/api/chat", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          messages: [...messages, userMessage],
          model_config: {
            model: "ai-pentest-agent",
            temperature: 0.7,
            max_tokens: 2000
          }
        }),
      });

      if (!response.ok) {
        throw new Error(`API request failed: ${response.status}`);
      }

      const data = await response.json();
      
      const assistantMessage: Message = {
        role: "assistant",
        content: data.message.content,
        metadata: data.message.metadata
      };

      setMessages((prev) => [...prev, assistantMessage]);

    } catch (err) {
      console.error("Error in handleSubmit:", err);
      setError(err instanceof Error ? err.message : "An unexpected error occurred");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card className="w-full max-w-4xl mx-auto">
      <div className="flex flex-col h-[600px]">
        <div className="p-4 border-b border-gray-800">
          {loading && (
            <div className="flex items-center gap-2 text-sm text-blue-400">
              <Loader2 className="h-4 w-4 animate-spin" />
              <span>Processing your request...</span>
            </div>
          )}
          
          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
        </div>

        <ScrollArea className="flex-1 p-4">
          <div className="space-y-4">
            {messages.map((message, index) => (
              <div
                key={index}
                className={`flex ${
                  message.role === "user" ? "justify-end" : "justify-start"
                }`}
              >
                <div
                  className={`
                    max-w-[80%] p-4 rounded-lg
                    ${message.role === "user" 
                      ? "bg-blue-600 text-white" 
                      : "bg-gray-800/50 border border-gray-700"
                    }
                  `}
                >
                  <div className="prose dark:prose-invert max-w-none">
                    {message.content}
                  </div>

                  {message.role === "assistant" && message.metadata?.plan_data && (
                    <div className="mt-4 space-y-4 border-t border-gray-700 pt-4">
                      <div className="flex justify-between items-center">
                        <div className="font-semibold text-blue-400">
                          Target: {message.metadata.plan_data.target}
                        </div>
                        <div className="text-sm text-gray-400">
                          {message.metadata.plan_data.estimated_time}
                        </div>
                      </div>
                      
                      {message.metadata.plan_data.warnings.length > 0 && (
                        <div className="bg-red-900/20 border border-red-800 rounded-lg p-3">
                          <div className="text-red-400 font-semibold mb-2">
                            ⚠️ Warnings:
                          </div>
                          <ul className="list-disc list-inside text-sm text-red-400 space-y-1">
                            {message.metadata.plan_data.warnings.map((warning, idx) => (
                              <li key={idx}>{warning}</li>
                            ))}
                          </ul>
                        </div>
                      )}

                      <div className="bg-blue-900/20 border border-blue-800 rounded-lg p-3">
                        <div className="text-blue-400 font-semibold mb-2">
                          Prerequisites:
                        </div>
                        <ul className="list-disc list-inside text-sm text-blue-400 space-y-1">
                          {message.metadata.plan_data.prerequisites.map((prereq, idx) => (
                            <li key={idx}>{prereq}</li>
                          ))}
                        </ul>
                      </div>

                      <div className="space-y-3">
                        {message.metadata.plan_data.plan.map((step, idx) => {
                          const riskStyles = {
                            low: "bg-green-900/20 text-green-400 border-green-800",
                            medium: "bg-yellow-900/20 text-yellow-400 border-yellow-800",
                            high: "bg-red-900/20 text-red-400 border-red-800"
                          }[step.risk_level];

                          return (
                            <div 
                              key={idx} 
                              className="bg-gray-800/50 border border-gray-700 rounded-lg p-3"
                            >
                              <div className="flex justify-between items-center mb-2">
                                <div className="font-semibold text-blue-400">
                                  {step.tool.toUpperCase()}
                                </div>
                                <div className={`text-xs px-2 py-1 rounded-full border ${riskStyles}`}>
                                  {step.risk_level.toUpperCase()}
                                </div>
                              </div>
                              <div className="font-mono text-sm bg-gray-900/50 p-2 rounded mb-2">
                                {step.command}
                              </div>
                              <div className="text-gray-300 text-sm">
                                {step.description}
                              </div>
                            </div>
                          );
                        })}
                      </div>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        </ScrollArea>

        <div className="p-4 border-t border-gray-800 bg-gray-900/50">
          <form onSubmit={handleSubmit} className="flex gap-2">
            <Input
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="Enter your pentest request..."
              className="flex-1"
              disabled={loading}
              autoComplete="off"
            />
            <Button 
              type="submit" 
              disabled={loading}
              className={`
                px-6 min-w-[120px]
                ${loading 
                  ? 'bg-gray-800 text-gray-400' 
                  : 'bg-blue-600 hover:bg-blue-700 text-white'
                }
              `}
            >
              {loading ? (
                <div className="flex items-center gap-2">
                  <Loader2 className="h-4 w-4 animate-spin" />
                  <span>Sending...</span>
                </div>
              ) : (
                "Send"
              )}
            </Button>
          </form>
        </div>
      </div>
    </Card>
  );
}
